"use client";

import React, { useState } from "react";
import ResumeEditor from "../components/Resume/ResumeEditor";
import ResumePreview from "../components/Resume/ResumePreview";

export default function Home() {
  const [resumeData, setResumeData] = useState({
    personalInfo: {
      name: "",
      email: "",
      phone: "",
      location: "",
      title: "",
    },
    summary: "",
    experience: [],
    education: [],
    skills: [],
  });

  const handleUpdateResume = (section: string, data: any) => {
    setResumeData((prev) => ({
      ...prev,
      [section]: data,
    }));
  };

  return (
    <main className="min-h-screen bg-gray-50">
      <div className="max-w-[1800px] mx-auto">
        <div className="flex flex-col md:flex-row min-h-screen">
          {/* Editor Panel */}
          <div className="w-full md:w-1/2 bg-white border-r border-gray-200 min-h-screen overflow-y-auto">
            <div className="sticky top-0 bg-white z-10 px-6 py-4 border-b border-gray-200">
              <h1 className="text-2xl font-bold text-gray-900">Resume Builder</h1>
              <p className="text-gray-500 mt-1">Create your professional resume</p>
            </div>
            <ResumeEditor resumeData={resumeData} onUpdate={handleUpdateResume} />
          </div>
          
          {/* Preview Panel */}
          <div className="w-full md:w-1/2 bg-gray-50 min-h-screen overflow-y-auto">
            <div className="sticky top-0 bg-gray-50 z-10 px-6 py-4 border-b border-gray-200">
              <h2 className="text-xl font-semibold text-gray-900">Preview</h2>
              <p className="text-gray-500 mt-1">See how your resume looks</p>
            </div>
            <ResumePreview resumeData={resumeData} />
          </div>
        </div>
      </div>
    </main>
  );
}
