"use client";

import React from "react";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";

interface ResumeEditorProps {
  resumeData: {
    personalInfo: {
      name: string;
      email: string;
      phone: string;
      location: string;
      title: string;
    };
    summary: string;
    experience: any[];
    education: any[];
    skills: string[];
  };
  onUpdate: (section: string, data: any) => void;
}

const ResumeEditor: React.FC<ResumeEditorProps> = ({ resumeData, onUpdate }) => {
  const handlePersonalInfoChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    onUpdate("personalInfo", {
      ...resumeData.personalInfo,
      [name]: value,
    });
  };

  return (
    <div className="p-6">
      <Tabs defaultValue="personal" className="w-full">
        <TabsList className="w-full grid grid-cols-5 gap-4 bg-gray-100 p-1 rounded-lg mb-6">
          <TabsTrigger 
            value="personal"
            className="data-[state=active]:bg-white data-[state=active]:shadow-sm"
          >
            Personal
          </TabsTrigger>
          <TabsTrigger 
            value="summary"
            className="data-[state=active]:bg-white data-[state=active]:shadow-sm"
          >
            Summary
          </TabsTrigger>
          <TabsTrigger 
            value="experience"
            className="data-[state=active]:bg-white data-[state=active]:shadow-sm"
          >
            Experience
          </TabsTrigger>
          <TabsTrigger 
            value="education"
            className="data-[state=active]:bg-white data-[state=active]:shadow-sm"
          >
            Education
          </TabsTrigger>
          <TabsTrigger 
            value="skills"
            className="data-[state=active]:bg-white data-[state=active]:shadow-sm"
          >
            Skills
          </TabsTrigger>
        </TabsList>

        <TabsContent value="personal">
          <Card className="p-6">
            <div className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="name">Full Name</Label>
                <Input
                  id="name"
                  name="name"
                  value={resumeData.personalInfo.name}
                  onChange={handlePersonalInfoChange}
                  placeholder="John Doe"
                  className="focus:ring-2 focus:ring-blue-500"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="title">Professional Title</Label>
                <Input
                  id="title"
                  name="title"
                  value={resumeData.personalInfo.title}
                  onChange={handlePersonalInfoChange}
                  placeholder="Software Engineer"
                  className="focus:ring-2 focus:ring-blue-500"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="email">Email</Label>
                <Input
                  id="email"
                  name="email"
                  type="email"
                  value={resumeData.personalInfo.email}
                  onChange={handlePersonalInfoChange}
                  placeholder="john@example.com"
                  className="focus:ring-2 focus:ring-blue-500"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="phone">Phone</Label>
                <Input
                  id="phone"
                  name="phone"
                  value={resumeData.personalInfo.phone}
                  onChange={handlePersonalInfoChange}
                  placeholder="+1 234 567 890"
                  className="focus:ring-2 focus:ring-blue-500"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="location">Location</Label>
                <Input
                  id="location"
                  name="location"
                  value={resumeData.personalInfo.location}
                  onChange={handlePersonalInfoChange}
                  placeholder="New York, USA"
                  className="focus:ring-2 focus:ring-blue-500"
                />
              </div>
            </div>
          </Card>
        </TabsContent>

        <TabsContent value="summary">
          <Card className="p-6">
            <div className="space-y-2">
              <Label htmlFor="summary">Professional Summary</Label>
              <Textarea
                id="summary"
                value={resumeData.summary}
                onChange={(e) => onUpdate("summary", e.target.value)}
                placeholder="Write a brief summary of your professional background and goals..."
                className="h-32 focus:ring-2 focus:ring-blue-500"
              />
            </div>
          </Card>
        </TabsContent>

        <TabsContent value="experience">
          <Card className="p-6">
            <div className="space-y-6">
              {resumeData.experience.map((exp, index) => (
                <div key={index} className="p-4 border rounded-lg space-y-4">
                  <div className="space-y-2">
                    <Label>Job Title</Label>
                    <Input
                      value={exp.title}
                      onChange={(e) => {
                        const newExp = [...resumeData.experience];
                        newExp[index] = { ...exp, title: e.target.value };
                        onUpdate("experience", newExp);
                      }}
                      placeholder="Software Engineer"
                      className="focus:ring-2 focus:ring-blue-500"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label>Company</Label>
                    <Input
                      value={exp.company}
                      onChange={(e) => {
                        const newExp = [...resumeData.experience];
                        newExp[index] = { ...exp, company: e.target.value };
                        onUpdate("experience", newExp);
                      }}
                      placeholder="Company Name"
                      className="focus:ring-2 focus:ring-blue-500"
                    />
                  </div>
                  <div className="grid grid-cols-2 gap-4">
                    <div className="space-y-2">
                      <Label>Start Date</Label>
                      <Input
                        type="month"
                        value={exp.startDate}
                        onChange={(e) => {
                          const newExp = [...resumeData.experience];
                          newExp[index] = { ...exp, startDate: e.target.value };
                          onUpdate("experience", newExp);
                        }}
                        className="focus:ring-2 focus:ring-blue-500"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label>End Date</Label>
                      <Input
                        type="month"
                        value={exp.endDate}
                        onChange={(e) => {
                          const newExp = [...resumeData.experience];
                          newExp[index] = { ...exp, endDate: e.target.value };
                          onUpdate("experience", newExp);
                        }}
                        className="focus:ring-2 focus:ring-blue-500"
                      />
                    </div>
                  </div>
                  <div className="space-y-2">
                    <Label>Description</Label>
                    <Textarea
                      value={exp.description}
                      onChange={(e) => {
                        const newExp = [...resumeData.experience];
                        newExp[index] = { ...exp, description: e.target.value };
                        onUpdate("experience", newExp);
                      }}
                      placeholder="Describe your responsibilities and achievements..."
                      className="h-24 focus:ring-2 focus:ring-blue-500"
                    />
                  </div>
                  <Button
                    variant="destructive"
                    onClick={() => {
                      const newExp = resumeData.experience.filter((_, i) => i !== index);
                      onUpdate("experience", newExp);
                    }}
                    className="mt-2"
                  >
                    Remove
                  </Button>
                </div>
              ))}
              <Button
                onClick={() =>
                  onUpdate("experience", [
                    ...resumeData.experience,
                    {
                      title: "",
                      company: "",
                      location: "",
                      startDate: "",
                      endDate: "",
                      description: "",
                    },
                  ])
                }
                className="w-full"
              >
                Add Experience
              </Button>
            </div>
          </Card>
        </TabsContent>

        <TabsContent value="education">
          <Card className="p-6">
            <div className="space-y-6">
              {resumeData.education.map((edu, index) => (
                <div key={index} className="p-4 border rounded-lg space-y-4">
                  <div className="space-y-2">
                    <Label>Degree</Label>
                    <Input
                      value={edu.degree}
                      onChange={(e) => {
                        const newEdu = [...resumeData.education];
                        newEdu[index] = { ...edu, degree: e.target.value };
                        onUpdate("education", newEdu);
                      }}
                      placeholder="Bachelor of Science in Computer Science"
                      className="focus:ring-2 focus:ring-blue-500"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label>School</Label>
                    <Input
                      value={edu.school}
                      onChange={(e) => {
                        const newEdu = [...resumeData.education];
                        newEdu[index] = { ...edu, school: e.target.value };
                        onUpdate("education", newEdu);
                      }}
                      placeholder="University Name"
                      className="focus:ring-2 focus:ring-blue-500"
                    />
                  </div>
                  <div className="grid grid-cols-2 gap-4">
                    <div className="space-y-2">
                      <Label>Start Date</Label>
                      <Input
                        type="month"
                        value={edu.startDate}
                        onChange={(e) => {
                          const newEdu = [...resumeData.education];
                          newEdu[index] = { ...edu, startDate: e.target.value };
                          onUpdate("education", newEdu);
                        }}
                        className="focus:ring-2 focus:ring-blue-500"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label>End Date</Label>
                      <Input
                        type="month"
                        value={edu.endDate}
                        onChange={(e) => {
                          const newEdu = [...resumeData.education];
                          newEdu[index] = { ...edu, endDate: e.target.value };
                          onUpdate("education", newEdu);
                        }}
                        className="focus:ring-2 focus:ring-blue-500"
                      />
                    </div>
                  </div>
                  <Button
                    variant="destructive"
                    onClick={() => {
                      const newEdu = resumeData.education.filter((_, i) => i !== index);
                      onUpdate("education", newEdu);
                    }}
                    className="mt-2"
                  >
                    Remove
                  </Button>
                </div>
              ))}
              <Button
                onClick={() =>
                  onUpdate("education", [
                    ...resumeData.education,
                    {
                      degree: "",
                      school: "",
                      location: "",
                      startDate: "",
                      endDate: "",
                      description: "",
                    },
                  ])
                }
                className="w-full"
              >
                Add Education
              </Button>
            </div>
          </Card>
        </TabsContent>

        <TabsContent value="skills">
          <Card className="p-6">
            <div className="space-y-2">
              <Label htmlFor="skills">Skills</Label>
              <Textarea
                id="skills"
                value={resumeData.skills.join(", ")}
                onChange={(e) => onUpdate("skills", e.target.value.split(", ").filter(Boolean))}
                placeholder="Enter your skills, separated by commas..."
                className="h-32 focus:ring-2 focus:ring-blue-500"
              />
              <p className="text-sm text-gray-500 mt-2">
                Separate skills with commas (e.g., JavaScript, React, Node.js)
              </p>
            </div>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
};

export default ResumeEditor;
